import { define, html } from '@pucelle/flit';
import { Popup, tooltip } from '@pucelle/flit-ui';
define('mm-text-editor-toolbar', class extends Popup {
    render() {
        let style = this.minderExported.getTextEditorSelectionStyle() || {};
        return html `
		<template class="mm-text-editor-toolbar">
			<ul>
				<li
					:style.font-weight=${style['font-weight'] || ''}
					:class.active=${style['font-weight'] === 'bold'}
					:enable=${this.canExec('TextBold')}
					${tooltip(html `Toggle Bold ${this.renderShortcutKey('TextBold')}`, { alignPosition: 'b' })}
					@click=${() => this.exec('TextBold')}
				>
					B
				</li>

				<li
					:style.font-style=${style['font-style'] || ''}
					:class.active=${style['font-style'] === 'italic'}
					:enable=${this.canExec('TextItalic')}
					${tooltip(html `Toggle Italic ${this.renderShortcutKey('TextItalic')}`, { alignPosition: 'b' })}
					@click=${() => this.exec('TextItalic')}
				>
					I
				</li>

				<li
					:style.text-decoration=${style['text-underline'] ? 'underline' : ''}
					:class.active=${style['text-underline'] ? 'underline' : ''}
					:enable=${this.canExec('TextUnderline')}
					${tooltip(html `Toggle Underline ${this.renderShortcutKey('TextUnderline')}`, { alignPosition: 'b' })}
					@click=${() => this.exec('TextUnderline')}
				>
					U
				</li>

				<li
					:style.text-decoration=${style['text-line-through'] ? 'line-through' : ''}
					:class.active=${style['text-line-through'] ? 'line-through' : ''}
					:enable=${this.canExec('TextStrikeThrough')}
					${tooltip(html `Toggle Strike Through ${this.renderShortcutKey('TextStrikeThrough')}`, { alignPosition: 'b' })}
					@click=${() => this.exec('TextStrikeThrough')}
				>
					S
				</li>

				<li
					:enable=${this.canExec('TextClearStyle')}
					${tooltip(html `Clear Style ${this.renderShortcutKey('TextClearStyle')}`, { alignPosition: 'b' })}
					@click=${() => this.exec('TextClearStyle')}
				>
					C
				</li>

				<li
					:enable=${this.canExec('TextSetKatex')}
					${tooltip(html `Add Math Expression ${this.renderShortcutKey('TextSetKatex')}`, { alignPosition: 'b' })}
					@click=${this.addMathExpression}
				>
					M
				</li>

			</ul>
		</template>
		`;
    }
    onConnected() {
        this.minderExported.on('editor-update-selection', this.update, this);
    }
    onDisconnected() {
        this.minderExported.off('editor-update-selection', this.update, this);
    }
    renderShortcutKey(commandName) {
        let keys = this.minderExported.commands.getShortcutKeys(commandName);
        if (!keys) {
            return '';
        }
        else {
            return html `<span>(${keys.join(', ')})</span>`;
        }
    }
    exec(commandName) {
        this.minderExported.commands.exec(commandName);
    }
    canExec(commandName) {
        return this.minderExported.commands.canExec(commandName);
    }
    addMathExpression() {
        this.emit('add-match-expression');
    }
});
