import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
define('mm-line-height-select', class extends Component {
    constructor() {
        super(...arguments);
        this.lineHeight = 1.2;
    }
    render() {
        return html `
		<template class="mm-line-height-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup', canShrinkInY: true })}
		>
			<mm-line-height .lineHeight=${this.lineHeight} />
		</template>
		`;
    }
    renderMenu() {
        return html `<mm-line-height-menu
			.herizontal
			.lineHeight=${this.lineHeight}
			@@select=${this.onSelectLineHeight}
		/>`;
    }
    onSelectLineHeight(lineHeight) {
        this.lineHeight = lineHeight;
        this.emit('select', lineHeight);
    }
});
define('mm-line-height', class extends Component {
    constructor() {
        super(...arguments);
        this.lineHeight = 1.2;
    }
    render() {
        return html `
		<template class="mm-line-height">
			${this.lineHeight}
		</template>
		`;
    }
});
define('mm-line-height-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.LineHeights = [
            1.0,
            1.1,
            1.2,
            1.3,
            1.4,
            1.5,
            1.6,
            1.7,
            1.8,
            1.9,
            2.0,
        ];
        this.lineHeight = 1.2;
    }
    render() {
        return html `
		<template class="mm-line-height-menu">
			<div class="mm-line-height-menu-scroller">
				${this.LineHeights.map(lineHeight => html `
						<mm-line-height
							.lineHeight=${lineHeight}
							:class.active=${lineHeight === this.lineHeight}
							@click=${() => this.selectLineHeight(lineHeight)}
						/>
					`)}
			</div>
		</template>
		`.extends(super.render());
    }
    selectLineHeight(lineHeight) {
        this.emit('select', lineHeight);
    }
});
