import { Component, define, html } from '@pucelle/flit';
import { Menu, popup, tooltip } from '@pucelle/flit-ui';
import { IconURLMap } from '../resources/icons';
define('mm-icon-select', class extends Component {
    constructor() {
        super(...arguments);
        this.iconNames = [];
    }
    render() {
        return html `
		<template class="mm-icon-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup', cacheable: true })}
		>
			${this.iconNames.map(name => html `
					<mm-icon .name=${name} />
				`)}
			
		</template>
		`;
    }
    renderMenu() {
        return html `
		<mm-icon-menu
			.herizontal
			.iconNames=${this.iconNames}
			@@select=${this.onSelectIcon}
		/>
		`;
    }
    onSelectIcon(iconName, willAdd) {
        this.emit('select', iconName, willAdd);
    }
});
define('mm-icon', class extends Component {
    constructor() {
        super(...arguments);
        this.name = '';
    }
    render() {
        return html `
			<template class="mm-icon">
				<img src=${IconURLMap[this.name]}>
			</template>
		`;
    }
});
define('mm-icon-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.iconNames = [];
    }
    render() {
        return html `
		<template class="mm-icon-menu">
			${Object.keys(IconURLMap).map(icon => html `
					<mm-icon
						.name=${icon}
						:class.active=${this.iconNames.includes(icon)}
						${tooltip(`Icon ${icon}`, { alignPosition: 't' })}
						@click=${() => this.onClickIcon(icon)}
					/>
				`)}
		</template>
		`.extends(super.render());
    }
    onClickIcon(iconName) {
        if (this.iconNames.includes(iconName)) {
            this.emit('select', iconName, false);
        }
        else {
            this.emit('select', iconName, true);
        }
    }
});
