import { Component, define, html } from '@pucelle/flit';
import { Menu, popup, tooltip } from '@pucelle/flit-ui';
define('mm-color-select', class extends Component {
    constructor() {
        super(...arguments);
        this.color = '';
    }
    render() {
        return html `
		<template class="mm-color-select"
			:style.background=${this.color}
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup' })}
		/>
		`;
    }
    renderMenu() {
        return html `<mm-color-menu .herizontal @@select=${this.onSelectColor} />`;
    }
    onSelectColor(color) {
        this.color = color;
        this.emit('select', color);
    }
});
define('mm-color-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.COLORS = [
            '#000000', '#424242', '#626262', '#9d9d95', '#c7c7c7', '#efefef', '#f7f7f7', '#ffffff',
            // .
            //'#ff0000', '#ff9d00', '#ffff00', '#00ff00', '#00ffff', '#0000ff', '#9d00ff', '#ff00ff',
            '#f7c7cf', '#ffebcb', '#ffebcb', '#d7efd7', '#d3dbe7', '#cfe7f7', '#d3dbe7', '#e7d7df',
            '#e79d9d', '#ffc79d', '#ffe79d', '#b5d7a5', '#a5c7cf', '#9dc7ef', '#b5a5d7', '#d7a5bd',
            '#e76262', '#f7ad6a', '#ffd762', '#95bd7a', '#72a5ad', '#6aaddf', '#8d7ac7', '#c77aa5',
            '#cf0000', '#e79538', '#efc730', '#6aa54a', '#4a7a8d', '#3885c7', '#624aa5', '#a54a7a',
            '#9d0000', '#b56208', '#bd9500', '#387a20', '#104a5a', '#085295', '#301872', '#721842',
            '#620000', '#7a3800', '#285218', '#285218', '#083038', '#003062', '#20104a', '#4a1030'
        ];
    }
    render() {
        return html `
		<template class="mm-color-menu">
			<ul>
			${this.COLORS.map(color => html `
					<li
						:style.background=${color}
						${tooltip(`Color ${color}`, { alignPosition: 't' })}
						@click=${() => this.onClickColor(color)}
					.>
				`)}
			</ul>
		</template>
		`.extends(super.render());
    }
    onClickColor(color) {
        this.emit('select', color);
    }
});
