import { html, Component, define } from '@pucelle/flit';
define('mm-zoom-show', class extends Component {
    onCreated() {
        this.minderExported.on('paper-view-transform-update', this.update, this);
    }
    render() {
        return html `
			<template class="mm-zoom-show">
				${Math.round(this.minderExported.commands.queryValue('ViewZoom') * 100)}%
			</template>
		`;
    }
});
