import { capitalize } from '@pucelle/ff';
import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
define('mm-connect-out-adjust-select', class extends Component {
    constructor() {
        super(...arguments);
        this.outAdjust = '';
    }
    render() {
        return html `
		<template class="mm-connect-out-adjust-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup' })}
		>
			<mm-connect-out-adjust .outAdjust=${this.outAdjust} />
		</template>
		`;
    }
    renderMenu() {
        return html `<mm-connect-out-adjust-menu
			.herizontal
			.outAdjust=${this.outAdjust}
			@@select=${this.onSelectOutAdjust}
		/>`;
    }
    onSelectOutAdjust(outAdjust) {
        this.outAdjust = outAdjust;
        this.emit('select', outAdjust);
    }
});
define('mm-connect-out-adjust', class extends Component {
    constructor() {
        super(...arguments);
        this.outAdjust = '';
    }
    render() {
        return html `
		<template class="mm-connect-out-adjust">
			${capitalize(this.outAdjust)}
		</template>
		`;
    }
});
define('mm-connect-out-adjust-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.outAdjustData = [
            'none',
            'evenly',
            'center',
        ];
        this.outAdjust = '';
    }
    render() {
        return html `
		<template class="mm-connect-out-adjust-menu">
			<div class="mm-connect-out-adjust-menu-scroller">
				${this.outAdjustData.map(outAdjust => html `
						<mm-connect-out-adjust
							.outAdjust=${outAdjust}
							:class.active=${outAdjust === this.outAdjust}
							@click=${() => this.selectoutAdjust(outAdjust)}
						/>
					`)}
			</div>
		</template>
		`.extends(super.render());
    }
    selectoutAdjust(outAdjust) {
        this.emit('select', outAdjust);
    }
});
