import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
define('mm-font-family-select', class extends Component {
    constructor() {
        super(...arguments);
        this.fontFamily = '';
    }
    render() {
        return html `
		<template class="mm-font-family-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup', canShrinkInY: true })}
		>
			<mm-font-family .fontFamily=${this.fontFamily} />
		</template>
		`;
    }
    renderMenu() {
        return html `<mm-font-family-menu
			.herizontal
			.fontFamily=${this.fontFamily}
			@@select=${this.onSelectFontFamily}
		/>`;
    }
    onSelectFontFamily(fontFamily) {
        this.fontFamily = fontFamily;
        this.emit('select', fontFamily);
    }
});
define('mm-font-family', class extends Component {
    constructor() {
        super(...arguments);
        this.fontFamily = '';
    }
    render() {
        return html `
		<template class="mm-font-family"
			:style.font-family=${this.fontFamily}
		>
			${this.fontFamily || 'Default Font'}
		</template>
		`;
    }
});
define('mm-font-family-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.FontFamilies = [
            'Arial',
            'Arial Black',
            'Book Antiqua',
            'Charcoal',
            'Comic Sans MS',
            'Consolas',
            'Courier',
            'Courier New',
            'cursive',
            'Gadget',
            'Geneva',
            'Georgia',
            'Helvetica',
            'Impact',
            'Lucida Grande',
            'Lucida Sans Unicode',
            'Monaco',
            'Palatino',
            'Palatino Linotype',
            'sans-serif',
            'Tahoma',
            'Times',
            'Times New Roman',
            'Trebuchet MS',
            'Verdana',
            "ADAM.CG PRO",
            "Abhaya Libre",
            "Agency FB",
            "Aladin",
            "Alegreya Sans SC",
            "Amandella Script",
            "Arizonia",
            "Bad Script",
            "Baloo Bhaina 2",
            "Barlow",
            "Barrio",
            "Berlin Sans FB",
            "BlowBrush",
            "Britannic",
            "Cabin Sketch",
            "Cursive Serif",
            "Dancing Script",
            "Dolce Vita",
            "Hamurz Free Version",
            "KG Broken Vessels Sketch",
            "Kaushan Script",
            "Montserrat Alternates",
            "NISCRIPT",
            "Permanent Marker",
            "Playlist",
            "SJNMDJW",
            "Source Sans Pro"
        ];
        this.fontFamily = '';
    }
    render() {
        return html `
		<template class="mm-font-family-menu">
			<div class="mm-font-family-menu-scroller">
				${this.FontFamilies.map(fontFamily => html `
						<mm-font-family
							.fontFamily=${fontFamily}
							:class.active=${fontFamily === this.fontFamily}
							@click=${() => this.selectFontFamily(fontFamily)}
						/>
					`)}
			</div>
		</template>
		`.extends(super.render());
    }
    selectFontFamily(fontFamily) {
        this.emit('select', fontFamily);
    }
});
