import { Component, define, html } from '@pucelle/flit';
import { Menu, popup, tooltip } from '@pucelle/flit-ui';
import { MinderExported } from 'mindmap-core';
define('mm-multi-color-select', class extends Component {
    constructor() {
        super(...arguments);
        this.multiColor = '';
    }
    render() {
        return html `
		<template class="mm-multi-color-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup' })}
		>
			<mm-multi-color .name=${this.multiColor} />
		</template>
		`;
    }
    renderMenu() {
        return html `
		<mm-multi-color-menu
			.herizontal
			.multiColor=${this.multiColor}
			@@select=${this.onSelectMultiColor}
		/>
		`;
    }
    onSelectMultiColor(color) {
        this.multiColor = color;
        this.emit('select', color);
    }
});
define('mm-multi-color', class extends Component {
    constructor() {
        super(...arguments);
        this.name = '';
    }
    render() {
        let colors = MinderExported.MultiColors[this.name] || ['#e0e0e0'];
        return html `
		<template class="mm-multi-color">
			${colors.map(color => html `
				<div :style.background=${color}></div>
			`)}
		</template>
		`;
    }
});
define('mm-multi-color-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.multiColor = '';
    }
    render() {
        return html `
		<template class="mm-multi-color-menu">
			<mm-multi-color @click=${() => this.onClickColor('')} />

			${Object.keys(MinderExported.MultiColors).map(multiColor => html `
					<mm-multi-color
						.name=${multiColor}
						:class.active=${multiColor === this.multiColor}
						${tooltip(`MultiColor ${multiColor}`, { alignPosition: 't' })}
						@click=${() => this.onClickColor(multiColor)}
					/>
				`)}
		</template>
		`.extends(super.render());
    }
    onClickColor(color) {
        this.emit('select', color);
    }
});
