import { capitalize } from '@pucelle/ff';
import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
define('mm-text-align-select', class extends Component {
    constructor() {
        super(...arguments);
        this.textAlign = 'center';
    }
    render() {
        return html `
		<template class="mm-text-align-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup', canShrinkInY: true })}
		>
			<mm-text-align .textAlign=${this.textAlign} />
		</template>
		`;
    }
    renderMenu() {
        return html `<mm-text-align-menu
			.herizontal
			.textAlign=${this.textAlign}
			@@select=${this.onSelectTextAlign}
		/>`;
    }
    onSelectTextAlign(textAlign) {
        this.textAlign = textAlign;
        this.emit('select', textAlign);
    }
});
define('mm-text-align', class extends Component {
    constructor() {
        super(...arguments);
        this.textAlign = 'center';
    }
    render() {
        return html `
		<template class="mm-text-align"
			:style.text-align=${this.textAlign}
		>
			${capitalize(this.textAlign)}
		</template>
		`;
    }
});
define('mm-text-align-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.TextAligns = [
            'left',
            'center',
            'right',
        ];
        this.textAlign = 'center';
    }
    render() {
        return html `
		<template class="mm-text-align-menu">
			<div class="mm-text-align-menu-scroller">
				${this.TextAligns.map(textAlign => html `
						<mm-text-align
							.textAlign=${textAlign}
							:class.active=${textAlign === this.textAlign}
							@click=${() => this.selectTextAlign(textAlign)}
						/>
					`)}
			</div>
		</template>
		`.extends(super.render());
    }
    selectTextAlign(textAlign) {
        this.emit('select', textAlign);
    }
});
