import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
define('mm-font-size-select', class extends Component {
    constructor() {
        super(...arguments);
        this.fontSize = 14;
    }
    render() {
        return html `
		<template class="mm-font-size-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup', canShrinkInY: true })}
		>
			<mm-font-size .fontSize=${this.fontSize} />
		</template>
		`;
    }
    renderMenu() {
        return html `<mm-font-size-menu
			.herizontal
			.fontSize=${this.fontSize}
			@@select=${this.onSelectFontSize}
		/>`;
    }
    onSelectFontSize(fontSize) {
        this.fontSize = fontSize;
        this.emit('select', fontSize);
    }
});
define('mm-font-size', class extends Component {
    constructor() {
        super(...arguments);
        this.fontSize = 14;
    }
    render() {
        return html `
		<template class="mm-font-size"
			:style.font-size.px=${this.fontSize}
		>
			${this.fontSize}
		</template>
		`;
    }
});
define('mm-font-size-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.FontSizes = [
            12,
            13,
            14,
            16,
            18,
            20,
            24,
            28,
            32,
            36,
            40,
        ];
        this.fontSize = 14;
    }
    render() {
        return html `
		<template class="mm-font-size-menu">
			<div class="mm-font-size-menu-scroller">
				${this.FontSizes.map(fontSize => html `
						<mm-font-size
							.fontSize=${fontSize}
							:class.active=${fontSize === this.fontSize}
							:style.line-height="1.5"
							@click=${() => this.selectFontSize(fontSize)}
						/>
					`)}
			</div>
		</template>
		`.extends(super.render());
    }
    selectFontSize(fontSize) {
        this.emit('select', fontSize);
    }
});
