import { define, html, untilRenderComplete } from '@pucelle/flit';
import { Popup } from '@pucelle/flit-ui';
import { MinderExported } from 'mindmap-core';
define('mm-text-katex-editor', class extends Popup {
    constructor() {
        super(...arguments);
        this.value = '';
        this.triangle = true;
        this.katexLibradyLoaded = false;
    }
    async onCreated() {
        //  Katex .
        await MinderExported.ExternalResources.ensureKatexResourcesLoaded();
        this.katexLibradyLoaded = true;
    }
    render() {
        return html `
			<template class="mm-text-katex-editor">
				<textarea
					:refElement=${this.refTextarea}
					:model="value"
					@keydown=${this.onKeyDown}
				/>
				<button
					:enable=${this.katexLibradyLoaded}
					@click=${this.submitValue}
				>
					Submit
				</button>
			</template>
		`.extends(super.render());
    }
    async refTextarea(el) {
        await untilRenderComplete();
        el.focus();
    }
    onKeyDown(e) {
        if (e.key === 'Enter') {
            this.submitValue();
            e.preventDefault();
        }
        else if (e.key === 'Escape') {
            this.emit('cancel');
        }
    }
    submitValue() {
        this.emit('change', this.value);
    }
});
