import { html, Component, define } from '@pucelle/flit';
import { popup } from '@pucelle/flit-ui';
define('mm-exporter', class extends Component {
    render() {
        return html `
			<template class="mm-exporter" ${popup(() => this.renderMenu())}>
				Export
			</template>
		`;
    }
    renderMenu() {
        return html `
			<f-menu class="mm-exporter-menu">
				<ul>
					<li @click=${() => this.exportAs('text')}>Text</li>
					<li @click=${() => this.exportAs('svg')}>SVG</li>
					<li @click=${() => this.exportAs('json')}>JSON</li>
					<li @click=${() => this.exportAs('jpeg')}>JPEG</li>
					<li @click=${() => this.exportAs('png')}>PNG</li>
					<!-- <li>Docx</li>
					<li>PDF</li> -->
				</ul>
			</f-menu>
		`;
    }
    exportAs(format) {
        this.minderExported.exportAsFile(format, 'gitmind', { expandAll: true, fillDefaultText: true });
    }
});
