    async updateSVG() {
        var _a;
        let svg = await MinderExported.layoutPreviewer.renderSVG(this.name, {
            subNodeCount: 0,
            style: {
                root: { 'fill-color': '#555455', 'radius': 20 },
                main: { 'fill-color': '#aca29d', 'radius': 15 },
            }
        });
        (_a = this.el.querySelector('svg')) === null || _a === void 0 ? void 0 : _a.remove();
        this.el.append(svg.element);
    }
