import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
import { MinderExported } from 'mindmap-core';
define('mm-relline-shape-select', class extends Component {
    render() {
        return html `
		<template class="mm-relline-shape-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup', cacheable: true })}
		>
			<mm-relline-shape .name=${this.shapeName} />
		</template>
		`;
    }
    renderMenu() {
        return html `
		<mm-relline-shape-menu
			.herizontal
			.shapeName=${this.shapeName}
			@@select=${this.onSelectShape}
		/>`;
    }
    onSelectShape(shapeName) {
        this.shapeName = shapeName;
        this.emit('select', shapeName);
    }
});
define('mm-relline-shape', class extends Component {
    render() {
        return html `
		<template class="mm-relline-shape">
			${this.name}
		</template>
		`;
    }
});
define('mm-relline-shape-menu', class extends Menu {
    render() {
        return html `
		<template class="mm-relline-shape-menu">
			${MinderExported.RelLineShapeNames.map(shapeName => html `
					<mm-relline-shape
						.name=${shapeName}
						:class.active=${shapeName === this.shapeName}
						@click=${() => this.onClickShape(shapeName)}
					/>
				`)}
			</ul>
		</template>
		`.extends(super.render());
    }
    onClickShape(shapeName) {
        this.emit('select', shapeName);
    }
});
