import { capitalize } from '@pucelle/ff';
import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
define('mm-text-vertical-align-select', class extends Component {
    constructor() {
        super(...arguments);
        this.textVerticalAlign = 'center';
    }
    render() {
        return html `
		<template class="mm-text-vertical-align-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup', canShrinkInY: true })}
		>
			<mm-text-vertical-align .textVerticalAlign=${this.textVerticalAlign} />
		</template>
		`;
    }
    renderMenu() {
        return html `<mm-text-vertical-align-menu
			.herizontal
			.textVerticalAlign=${this.textVerticalAlign}
			@@select=${this.onSelectTextVerticalAlign}
		/>`;
    }
    onSelectTextVerticalAlign(textVerticalAlign) {
        this.textVerticalAlign = textVerticalAlign;
        this.emit('select', textVerticalAlign);
    }
});
define('mm-text-vertical-align', class extends Component {
    constructor() {
        super(...arguments);
        this.textVerticalAlign = 'center';
    }
    render() {
        return html `
		<template class="mm-text-vertical-align">
			${capitalize(this.textVerticalAlign)}
		</template>
		`;
    }
});
define('mm-text-vertical-align-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.TextVerticalAligns = [
            'top',
            'center',
            'bottom',
        ];
        this.textVerticalAlign = 'center';
    }
    render() {
        return html `
		<template class="mm-text-vertical-align-menu">
			<div class="mm-text-vertical-align-menu-scroller">
				${this.TextVerticalAligns.map(textVerticalAlign => html `
						<mm-text-vertical-align
							.textVerticalAlign=${textVerticalAlign}
							:class.active=${textVerticalAlign === this.textVerticalAlign}
							@click=${() => this.selectTextVerticalAlign(textVerticalAlign)}
						/>
					`)}
			</div>
		</template>
		`.extends(super.render());
    }
    selectTextVerticalAlign(textVerticalAlign) {
        this.emit('select', textVerticalAlign);
    }
});
