import { Component, define, html } from '@pucelle/flit';
import { Menu, popup } from '@pucelle/flit-ui';
define('mm-border-width-select', class extends Component {
    constructor() {
        super(...arguments);
        this.width = 0;
    }
    render() {
        return html `
		<template class="mm-border-width-select"
			${popup(() => this.renderMenu(), { alignPosition: 'l', key: 'popup' })}
		>
			<mm-border-width .width=${this.width} />
		</template>
		`;
    }
    renderMenu() {
        return html `<mm-border-width-menu
			.herizontal
			.width=${this.width}
			@@select=${this.onSelectBorderWidth}
		/>`;
    }
    onSelectBorderWidth(borderWidth) {
        this.width = borderWidth;
        this.emit('select', borderWidth);
    }
});
define('mm-border-width', class extends Component {
    constructor() {
        super(...arguments);
        this.width = 0;
    }
    render() {
        return html `
		<template class="mm-border-width">
			<div :style.height.px=${this.width} />
		</template>
		`;
    }
});
define('mm-border-width-menu', class extends Menu {
    constructor() {
        super(...arguments);
        this.width = 0;
        this.BorderWidths = [
            0,
            1,
            2,
            3,
            4,
            5,
        ];
    }
    render() {
        return html `
		<template class="mm-border-width-menu">
			${this.BorderWidths.map(borderWidth => html `
					<mm-border-width
						.width=${borderWidth}
						:class.active=${borderWidth === this.width}
						@click=${() => this.onClickBorderWidth(borderWidth)}
					/>
				`)}
		</template>
		`.extends(super.render());
    }
    onClickBorderWidth(borderWidth) {
        this.emit('select', borderWidth);
    }
});
