import { Component, define, html } from '@pucelle/flit';
define('mm-birdeye-previewer', class extends Component {
    onCreated() {
        let previewer = this.minderExported.requestBirdEyePreview();
        previewer.appendTo(this.el);
    }
    render() {
        return html `
			<template class="mm-birdeye-previewer" />
		`;
    }
});
